<template>
    <div class="goodsCollection flex-page">
        <navigation-bar  @onLeftClick="goBack" pageName="适用商品列表" >
            <slot slot="nav-right" class="rightBar" name="right">
                <div class="rightBar" @click="goToShopCart">
                    <img ref="cartIcon" class="shopIcon" src="@/assets/images/newSlicing/icon_shop_cut.png" alt="">
                    <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                        {{skuTypeNum<=99?skuTypeNum:'99+'}}
                    </div>
                </div>
            </slot>
        </navigation-bar>
        <div class="couponTitleBox">
            <div class="couponTitle">以下商品可使用{{couponName}}的平台红包</div>
            <div class="sortBtnList">
                <div class="volumeBox" @click="clickSaleBtn()">
                    <div class="volumeBtn" :class="sortMethod==2?'redLineBox':''">销量</div>
                </div>
                <div @click="priceSort">
                    <div class="priceBtn"><div class="priceTxt" :class="sortMethod!=2?'redLineBox':''">价格</div><img :src="sortMethod==2?icon.filter:(sortMethod==1?icon.filterDown:icon.filterUp)" alt=""></div>
                </div>
            </div>
        </div>
        <div class="post-wrapper" ref="contentScroll">
            <van-list
                    v-model="loading"
                    :finished="finished"
                    @load="onLoad"
                    :offset="500"
                    :immediate-check="immediateBol"
            >
                <div class="goodsList">
                    <div class="goodsItem" v-for="(goodsItem,goodsIndex) in goodsListData"
                             :key="'goodsIndex'+goodsIndex" @click="goToProduct(goodsItem)">
                            <div class="goodsImg">
                                <img :src="goodsItem.spuPicUrl" alt="">
                                <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                <div class="onlyRemainTxt" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                                <div class="tag-group" v-if="goodsItem.isAlive==1" @click="gotoLive">
                                    <div class="live-tag">
                                        <div class="circle"></div>
                                        直播中
                                    </div>
                                    <div class="live-num">{{ goodsItem.viewersNum || 0 }}</div>
                                </div>
                            </div>
                            <div class="goodsInfo">
                                <div class="goodsName">{{goodsItem.spuName}}</div>
                                <div class="labelList">
                                    <div class="labelItem" :class="goodsItem.shopName.length>8?'labelItemOver':''">{{goodsItem.shopName}}</div>
                                    <div class="labelItem ticketLabelItem" v-show="goodsItem.couponFlag==1">券</div>
                                </div>
                                <div class="goodsBottomInfo">
                                    <div class="goodsPrice">
                                        <div class="priceAndSale">
                                            <div class="goodsPriceNum">￥{{goodsItem.skuPrice | priceFilters}}</div>
                                            <!-- <div class="saleNum">月售{{goodsItem.virtualSale | threshold}}</div> -->
                                        </div>
                                        <div class="lineGoodsPriceNum" v-if="goodsItem.priceLine">￥{{goodsItem.priceLine|priceFilters}}</div>
                                    </div>
                                    <div  class="addBtn" @click.stop="addGoodsNum(goodsItem,goodsIndex)">
                                        <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''">
                                            <div class="cartNum">{{goodsItem.cartCount}}</div>
                                        </div>
                                        <img ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt="">
                                        <!-- <img v-show="goodsItem.storeNumber!=0" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt=""> -->
                                        <!-- <img v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt=""> -->


                                    </div>
                                    <!-- <div v-show="goodsItem.storeNumber==0"  class="addBtn">
                                        <img src="@/assets/images/mall/mall_icon_add_gray.png" alt="">
                                    </div> -->
                                </div>

                            </div>
                        </div>
                    <div class="finishedBox" v-show="finished&&goodsListData.length!=0">
                        <img src="@/assets/images/mall/mall_logo.png" alt="">
                    </div>
                </div>
                <div class="fixedHeight" :style="{height:30+44+this.$footBarHeight+'px'}"></div>
            </van-list>
        </div>
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
        </transition>
    </div>
</template>


<script>
    import {
        getActSpuListService,
        getSkuTypeNumService,
        addGoodsService,
        getGoodsByShopsService,
    } from "../../services/goodsService";
    import { Toast, Dialog} from "vant";
    import NavigationBar from "@/common/currency/NavigationBar.vue";
    export default {
        name: "goodsCollection",
        data(){
            return{
                couponName:"",
                skuTypeNum:null,
                sortMethod:2, // 2 销量 0 从小到大 1从大到小
                isPriceSort:false, // false 从小到大 true 从大到小
                icon:{
                    filter:require("@/assets/images/newSlicing/icon_filter.png"),
                    filterDown:require("@/assets/images/newSlicing/icon_filter_down.png"),
                    filterUp:require("@/assets/images/newSlicing/icon_filter_up.png"),
                },
                goodsListData:null, //商品列表
                scrollContentTop:null,
                shopCodeList:null,

                addBtnIcon:null,
                addGoodsLoading:false,
                isShowCircle:false,

                page:1,
                loading:false,
                finished:false,
                immediateBol:false,
                isOrderPayNew:false,
            }
        },
        components: {
            NavigationBar
        },
        filters: {
            threshold(data) {
                return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
            },
            priceFilters(data){
                return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
            }
        },
        created(){

        },
        watch: {
            /*监控到物理返回键点击状态*/
            shopPageEventBackButtonFlag(val) {
                if (val) {
                    this.goBack();
                    this.$store.dispatch('setShopPageEventBackButtonFlag', false);
                }
            }
        },
        activated(){
            if (this.$route.params.couponName) {
                this.goodsListData= [];
                this.page = 1;
                this.finished = false;
                this.sortMethod = 2;
                this.isPriceSort = false;
                this.couponName = this.$route.params.couponName;
                this.shopCodeList = this.$route.params.shopCodeList;
                if(this.$route.params.isOrderPayNew){
                    this.isOrderPayNew = this.$route.params.isOrderPayNew
                }else{
                    this.isOrderPayNew = false;
                }
                this.$refs.contentScroll.scrollTop = 0;
                this.getGoodsByShops();
            }else{
                this.$refs.contentScroll.scrollTop = this.scrollContentTop;
            }
            this.getSkuTypeNum();
        },
        methods:{
            //销量按钮
            clickSaleBtn(){
                this.sortMethod = 2;
                this.isPriceSort = false;
                this.page = 1;
                this.getGoodsByShops();
            },
            //价格
            priceSort(){
                if (!this.isPriceSort) {
                    this.sortMethod = 0;
                } else {
                    this.sortMethod = 1;
                }
                this.isPriceSort = !this.isPriceSort;
                this.page = 1;
                this.getGoodsByShops();
            },
            onLoad(){
                this.page++;
                this.getGoodsByShops(false);
            },
            /** 根据活动编码查活动商品
             * actCode   活动编码
             * ownerCode 业主编码
             * */
            getGoodsByShops(isFirst = true){
                let paramData = {
                    shopCodes:this.shopCodeList,
                    areaCode:this.$userInfo.AREA_CODE,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    sort: this.sortMethod,
                    page:this.page,
                    rows:10,
                }
                getGoodsByShopsService(paramData).then(res => {
                    if (res.resultCode == "0") {
                        this.loading = false;
                        if(isFirst){
                            this.goodsListData = res.data.records;
                        }else{
                            this.goodsListData = this.goodsListData.concat(res.data.records);
                        }
                        this.finished = this.goodsListData.length>=res.data.total?true:false;
                    }
                }).catch(error => {
                })
            },
            /**
             * 查询购物车数量
             * ownerCode  Number  业主CODE
             */
            getSkuTypeNum(isAddGoods=false){
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE
                }
                getSkuTypeNumService(paramData).then(res=>{
                    if (res.resultCode == 0) {
                        if (isAddGoods){
                            Toast({
                                message: '添加购物车成功',
                                duration:1000
                            });
                        }
                        this.skuTypeNum = res.data.skuTypeNum;
                    }
                }).catch(error=>{
                })
            },
            // 进入前状态
            beforeEnter(el) {
                console.log(this.addBtnIcon.getBoundingClientRect())
                el.style.left = this.addBtnIcon.getBoundingClientRect().left + 'px';
                el.style.top = this.addBtnIcon.getBoundingClientRect().top + 'px';
                el.style.transform = 'scale(1)'
            },
            // 进入中
            enter(el, done) {
                // el.offsetWidth 强制html渲染动画
                // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
                el.offsetWidth;
                el.style.left = this.$refs.cartIcon.getBoundingClientRect().left + 'px'
                el.style.top = this.$refs.cartIcon.getBoundingClientRect().top + 'px'
                el.style.transform = 'scale(.5)'
                done()
            },
            afterEnter(el) {
                this.isShowCircle = !this.isShowCircle
            },
            //点击加号，增加商品数量
            addGoodsNum(goodsItem, goodsIndex) {
                console.log(goodsItem)
                if (this.addGoodsLoading || this.isShowCircle) {
                    return;
                }
                this.addBtnIcon = this.$refs.addBtn[goodsIndex];
                this.isShowCircle = true;
                this.addGoodsLoading = true;
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    spuCode: goodsItem.spuCode,
                    skuNo: goodsItem.skuNo,
                    shopCode: goodsItem.shopCode,
                    activitySpuId: goodsItem.activitySpuId,
                    skuCount: 1,
                    mark: 1,
                }
                addGoodsService(paramData).then(res => {
                    if (res.resultCode == "0") {
                        Toast({
                            message: '添加购物车成功',
                            duration: 1000
                        });
                        goodsItem.cartCount += 1;
                        this.getSkuTypeNum();
                    }else if (res.resultCode == '20100' && res.data.result == '0') {
                        this.$toast('您所添加商品超过最大购买量了哦');
                    }
                    this.addGoodsLoading = false;
                }).catch(error => {
                    this.addGoodsLoading = false;
                })
            },
            //去商品详情页
            goToProduct(goodsItem) {
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode: goodsItem.spuCode,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            },
            goToShopCart(){
                this.$router.push({
                    name: 'shoppingCart',
                    params: {}
                })
            },
            goBack() {
                if(this.isOrderPayNew){
                    this.$router.push({
                        name: 'productPage',
                        params: {}
                    })
                }else{
                    this.$router.back(-1);
                }
            }
        },
        beforeRouteLeave(to, form, next) {
            this.scrollContentTop = this.$refs.contentScroll.scrollTop;
            next();
        }
    }
</script>

<style scoped lang="scss">
    .circle{
        width: 22px;
        height: 22px;
        position: fixed;
        z-index: 100;
        transition: all .3s ease;
    }
    .flex-page{
        width: 100%;
        height: 100%;
        position: absolute;
        .post-wrapper{
            height: 100%;
            overflow-y: scroll;
            background: #f9f9f9;
        }
        .post-wrapper::-webkit-scrollbar {
            display: none;
        }
    }
    /deep/.navBar {
        height: 44PX!important;
        display: flex;
        align-items: center;
        .left,.right{
            height: 44PX!important;
        }
        .right{
            right: 5px;
            width: 44px!important;
            height: 44px!important;
            /*background: green;*/
            bottom: auto;
        }
    }
    .rightBar {
        width: 44px;
        height: 44px;
        position: relative;

        img {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 50%; /*偏移*/
            left: 0;
            transform: translateY(-50%);
        }
        .goodsNum {
            position: absolute;
            font-size: 0.26667rem;
            color: #ffffff;
            text-align: center;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            background-color: #FC0238;
            z-index: 2;
            top: 0.13333rem;
            left: 0.42667rem;
            width: -webkit-max-content;
            width: -moz-max-content;
            width: max-content;
            min-width: 0.42667rem;
            height: 0.42667rem;
            line-height: 0.42667rem;
            border-radius: 0.21333rem;
        }
        .goodsNumMax{
            padding: 0 3px;
            min-width: 26px;
        }
    }
    .couponTitleBox{
        background: #f9f9f9;
        height: 20PX;
        padding: 5px 15px 10PX;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .couponTitle{
            font-family: PingFangSC-Regular;
            font-size: 12px;
            color: #999999;
            letter-spacing: 0;
        }
    }
    .sortBtnList {
        display: flex;
        justify-content: flex-end;

        .volumeBox {
            margin-right: 25px;
        }

        .volumeBtn {
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #999999;
            letter-spacing: -0.04px;
            line-height: 18px;
        }

        .priceBtn {
            display: flex;
            align-items: center;
            .priceTxt{
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #999999;
                letter-spacing: -0.04px;
                line-height: 18px;
            }
            img {
                width: 5px;
                margin-left: 2px;
            }
        }
        .redLineBox {
            color: #4a4a4a !important;
            font-weight: 600;
        }
    }
    .goodsList{
        padding: 0 15px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .goodsItem{
        width: 168px;
        background: #FFFFFF;
        box-shadow: 0 0 5px 0 rgba(194,194,194,0.50);
        border-radius: 4px;
        margin-bottom: 10px;
        .goodsImg{
            width: 100%;
            height: 168px;
            overflow: hidden;
            position: relative;
            border-radius: 4px 4px 0 0;
            img{
                width: 100%;
            }
            .outSellBox{
                width: 70px;
                height: 70px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                border-radius: 50%;
                background: rgba(0, 0, 0, 0.50);
                font-size: 12px;
                color: #FFFFFF;
                letter-spacing: -0.04px;
                line-height: 70px;
                text-align: center;
            }
            .onlyRemainTxt{
                width: 100%;
                height: 25px;
                text-align: center;
                position: absolute;
                bottom: 0;
                background: rgba(0, 0, 0, 0.50);
                font-size: 12px;
                color: #FFFFFF;
                letter-spacing: -0.04px;
                line-height: 25px;
            }
            .tag-group {
                position: absolute;
                left: 75px;
                top: 5px;
                border-radius: 3px;
                padding: 0 5px 0 0;
                height: 18px;
                line-height: 18px;
                background: rgba(0, 0, 0, 0.49);
            }
            .live-num {
                font-size: 12px;
                color: #ffffff;
                float: right;
            }
            .live-tag {
                background-image: linear-gradient(-144deg, #ff5c6f 0%, #ff2d3f 57%);
                border-radius: 3px;
                font-size: 12px;
                color: #ffffff;
                padding: 0 4px 0 10px;
                margin: 0 7px 0 0;
                width: 38px;
                height: 18px;
                float: left;
                .circle {
                    position: absolute;
                    top: 50%;
                    left: 10%;
                    transform: translate(-50%,-50%);
                    width: 1px;
                    height: 1px;
                    background: #fff;
                    border-radius: 50%;
                    border: 1px solid #fff;
                }

            }
        }
        .goodsInfo{
            padding: 3px 5px 5px;
            .goodsName{
                height: 40px;
                line-height: 20px;
                font-family: PingFangSC-Medium;
                font-size: 14px;
                color: #333333;
                letter-spacing: -0.05px;
                display: -webkit-box;
                overflow: hidden;
                text-overflow: ellipsis;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
            .labelList{
                display: flex;
                flex-wrap: wrap;
                margin-top: 5px;
                .labelItem{
                    max-width: 130px;
                    position: relative;
                    padding: 1PX 3PX;
                    margin-right: 0;
                    height: 17PX;
                    line-height: 17PX;
                    display: flex;
                    align-items: center;
                    font-size: 10px;
                    color: #e4393c;
                    background: #fff;
                    word-break: break-word;
                    font-style: normal;
                    vertical-align: middle;
                    -webkit-tap-highlight-color: rgba(0,0,0,0);
                    outline: 0;
                    box-sizing: border-box;
                }
                .labelItemOver{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .labelItem:before {
                    content: "";
                    display: block;
                    border: 1PX solid #ddd;
                    position: absolute;
                    top: 0;
                    bottom: -100%;
                    left: 0;
                    right: -99.5%;
                    pointer-events: none;
                    border-color: #e4393c;
                    border-radius: 6px;
                    transform: scale(0.5);
                    -webkit-transform: scale(0.5);
                    transform-origin: 0 0;
                    -webkit-transform-origin: 0 0;
                }
                .ticketLabelItem{
                    margin-left: 5px;
                }
            }
            .goodsBottomInfo{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 5px;
                padding-right: 5px;
                .goodsPrice{
                    .priceAndSale{
                        display: flex;
                        align-items: center;
                        .goodsPriceNum{
                            font-family: PingFangSC-Medium;
                            font-size: 14px;
                            color: #F43D3D;
                            letter-spacing: -0.04px;
                            line-height: 14px;
                            margin-right: 6px;
                        }
                        .saleNum{
                            font-family: PingFangSC-Regular;
                            font-size: 10px;
                            color: #9A9A9A;
                            letter-spacing: -0.03px;
                            line-height: 10px;
                        }
                    }
                    .lineGoodsPriceNum{
                        font-family: PingFangSC-Regular;
                        font-size: 10px;
                        color: #9A9A9A;
                        letter-spacing: -0.03px;
                        line-height: 10px;
                        text-decoration: line-through;
                        margin-top: 3px;
                        padding-left: 1px;
                    }
                }
                .addBtn{
                    width: 22px;
                    height: 22px;
                    display: flex;
                    position: relative;
                    .cartNumBox{
                        width: 18px;
                        height: 18px;
                        position: absolute;
                        top: -9px;
                        right: -9px;
                        background: #fff;
                        border-radius: 50%;
                        .cartNum{
                            width: 16px;
                            height: 16px;
                            margin: 1px auto 0px;
                            box-sizing: border-box;
                            border-radius: 50%;
                            border: 1px solid #FB5768;
                            font-family: PingFangSC-Semibold;
                            font-size: 10px;
                            color: #FB5768;
                            letter-spacing: -0.03px;
                            text-align: center;
                            line-height: 16px;
                        }

                    }
                    .cartNumBigBox{
                        width: 28px;
                        height: 18px;
                        border-radius: 30px;
                        .cartNum{
                            width: 26px;
                            height: 16px;
                            border-radius: 30px;
                            margin: 1px auto 0px;
                            box-sizing: border-box;
                            border: 1px solid #FB5768;
                            font-family: PingFangSC-Semibold;
                            font-size: 10px;
                            color: #FB5768;
                            letter-spacing: -0.03px;
                            text-align: center;
                            line-height: 15px;
                        }
                    }
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
    .finishedBox{
        width: 100%;
        padding-bottom: 10px;
        text-align: center;
        img{
            width: 75px;
            height: 21px;
        }
    }
</style>
